.out{
	 h1,h2,h3,h4,h5,h6,span,p{
		 word-break: break-word;
	 }
}

.out h2{
	color: #232323;
	font: 42px $fl;
	text-align: center;
	position: relative;
	padding-bottom: 22px;
	margin-bottom: 45px;
	&:before,
	&:after{
		content: '';
		position: absolute;
		left: 50%;
		margin-left: -185.5px;
	}
	&:before{
		width: 371px;
		height: 1px;
		background: rgba(0,0,0,0.12);
		bottom: 0;
	}
	&:after{
		height: 3px;
		background: #efe202;
		width: 27px;
		left: 50%;
		margin-left: -13.5px;
		bottom: -2px;
	}
}
.grey_btn{
	background-color: #f2f2f2;
	border-radius: 2px;
	color: #232323;
	font: 14px $fb;
	display: inline-block;
	width: 100%;
	text-align: center;
	line-height: 1;
	padding: 18px 2px;
	transition: all .2s ease;
	&:hover{
		background: #F2F2DC;
		text-decoration: none;
		color: black;
	}
}
.list_tab{
	font-size: 0;
	margin-bottom: 45px;
	li{
		display: inline-block;
		margin-right: 10px;
		margin-bottom: 10px;
		&:last-of-type{
			margin-right: 0;
		}
		a{
			color: #232323;
			text-decoration: none;
			font: 14px $font;
			font-weight: 300;
			display: block;
			padding: 12px 10px;
