.toggle_mnu {
  position: relative;
  width: 21px;
  display: inline-block;
  z-index: 1;
  cursor: pointer;
  height: 18px;
  border: none;
  background: transparent;
  outline: none;
}

.toggle_mnu .sandwich {
  width: 21px;
  height: 18px;
  position: absolute;
  top: 0px;
  bottom: 0;
  left: 0;
  z-index: 200;
}

.toggle_mnu .sandwich .sw-topper {
  position: relative;
  top: 0;
  width: 21px;
  height: 3px;
  background: white;
  border: none;
  border-radius: 4px 4px 4px 4px;
  transition: top 0.2s, -webkit-transform 0.5s;
  transition: transform 0.5s, top 0.2s;
  transition: transform 0.5s, top 0.2s, -webkit-transform 0.5s;
  display: block;
}

.toggle_mnu .sandwich .sw-bottom {
  position: relative;
  width: 21px;
  height: 3px;
  top: 4.2px;
  background: white;
  border: none;
  border-radius: 4px 4px 4px 4px;
  transition: top 0.2s, -webkit-transform 0.5s;
  transition: transform 0.5s, top 0.2s;
  transition: transform 0.5s, top 0.2s, -webkit-transform 0.5s;
  transition-delay: 0.2s, 0s;
  display: block;
}

.toggle_mnu .sandwich .sw-footer {
  position: relative;
  width: 21px;
  height: 3px;
  top: 8px;
  background: white;
  border: none;
  border-radius: 4px 4px 4px 4px;
  transition: all 0.5s;
  transition-delay: 0.1s;
  display: block;
}

.toggle_mnu .active .sw-topper {
  top: 9px;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.toggle_mnu .active .sw-bottom {
  top: 6px;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.toggle_mnu .active .sw-footer {
  opacity: 0;
  top: 0;
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}
